@include ipad-landscape {
    
    body:not(.qodef-content-is-behind-header):not(.error404) .qodef-content {
        margin-top: 0 !important;
    }
    
    .qodef-sidebar-holder {
        margin-top: 60px;
    }
    
    .single-destinations .qodef-sidebar-holder {
        padding-top: 38px;
    }
}

@include ipad-landscape {
    
    .qodef-hide-on-tablet {
        display: none !important;
    }
}

@include phone-landscape {
    
    .qodef-hide-on-mobile {
        display: none !important;
    }
}

.qodef-grid,
.qodef-container-inner,
.qodef-row-grid-section {

    @include laptop-landscape {
        width: $grid-width-laptop-landscape;
    }

    @include ipad-landscape {
        width: $grid-width-ipad-landscape;
    }
	
	@media only screen and (min-width: $ipad-portrait-plus-pixel) and (max-width: 850px) {
		width: 700px;
	}
	
	@include ipad-portrait {
        width: $grid-width-ipad-portrait;
    }

    @include phone-landscape {
        width: $grid-width-phone-landscape;
    }

    @include phone-portrait {
        width: $grid-width-phone-portrait;
    }

    @include smaller-phone-portrait {
        width: $grid-width-smaller-phone-portrait;
    }
}

.qodef-boxed .qodef-wrapper .qodef-wrapper-inner,
.qodef-boxed .qodef-wrapper .qodef-footer-inner {

    @include laptop-landscape {
        width: $grid-width-laptop-landscape-boxed;
    }

    @include ipad-landscape {
        width: $grid-width-ipad-landscape-boxed;
    }

    @include ipad-portrait {
        width: $grid-width-ipad-portrait-boxed;
    }

    @include phone-landscape {
        width: $grid-width-phone-landscape-boxed;
    }

    @include phone-portrait {
        width: $grid-width-phone-portrait-boxed;
    }

    @include smaller-phone-portrait {
        width: $grid-width-smaller-phone-portrait-boxed;
    }
}

@include ipad-landscape {
	
	.page-template-search-tour-item-template {
		
		&.qodef-page-content-is-boxed {
			
			.qodef-content {
				
				.qodef-content-inner {
					
					.qodef-container {
						
						.qodef-container-inner {
							padding: 0 30px 70px;
						}
					}
				}
			}
			
			&.qodef-content-overlapping {
				
				.qodef-title-holder {
					
					.qodef-title-inner {
						padding-bottom: 30px;
					}
				}
				
				.qodef-content {
					
					.qodef-content-inner {
						
						.qodef-container {
							
							.qodef-container-inner {
								@include qodefTransform(translateY(-40px));
							}
						}
					}
				}
			}
			
			.qodef-search-ordering-holder {
				
				.qodef-search-ordering-list {
					margin: 0 -45px 30px;
				}
			}
		}
	}
    
    .qodef-page-content-is-boxed {
        
        &.qodef-content-overlapping {
            
            .qodef-title-holder {
                
                .qodef-title-inner {
                    padding-bottom: 40px;
                }
            }
            
            .qodef-content {
                
                .qodef-content-inner {
                    
                    > .qodef-container {
                        
                        > .qodef-container-inner {
                            margin-top: -40px;
                        }
                    }
                }
            }
        }
        
        .qodef-content {
            
            .qodef-content-inner {
                
                > .qodef-container {
                    
                    > .qodef-container-inner {
                        padding: 30px 30px 120px;
                    }
                }
            }
        }
    }
    
    .archive {
        
        &.qodef-page-content-is-boxed {
            
            .qodef-content {
                
                .qodef-content-inner {
                    
                    > .qodef-container {
                        
                        > .qodef-container-inner {
                            padding: 30px 30px 70px;
                            
                        }
                    }
                    
                    .qodef-tours-search-page-holder {
                        
                        .qodef-container {
                            
                            > .qodef-container-inner {
                                padding: 30px 30px 70px;
                            }
                        }
                    }
                }
            }
            
            &.qodef-content-overlapping {
                
                .qodef-content {
                    
                    .qodef-content-inner {
                        
                        .qodef-tours-search-page-holder {
                            
                            .qodef-container {
                                
                                > .qodef-container-inner {
                                    @include qodefTransform(translateY(-40px));
                                }
                            }
                        }
                    }
                }
            }
            
            .qodef-search-ordering-holder {
                
                .qodef-search-ordering-list {
                    margin: -30px -45px 30px;
                }
            }
        }
    }
}